.gym {
  padding: 120px 0 154px 0;

  @include vp-1199 {
    padding: 105px 0;
  }

  @include vp-767 {
    padding: 70px 0;
  }
}

.gym__wrapper {
  display: flex;
  flex-wrap: wrap;

  @include vp-1199 {
    flex-direction: column;
  }
}

.gym__info {
  max-width: 415px;
  padding: 45px 0 0 60px;
  margin-right: 85px;

  @include vp-1199 {
    max-width: none;
    padding: 0;
    margin: 0 0 43px 0;
    text-align: center;
  }

  @include vp-767 {
    margin-bottom: 40px;
    text-align: left;
  }
}

.gym__info h2 {
  font-family: $ff-alumni-sans;
  font-weight: 600;
  font-size: 48px;
  line-height: 46px;
  margin: 0 0 17px 0;
  letter-spacing: 0.03em;

  @include vp-1199 {
    max-width: 480px;
    margin: 0 auto 7px auto;
    font-size: 42px;
    line-height: 42px;
  }

  @include vp-767 {
    font-size: 32px;
    line-height: 32px;
    max-width: none;
    margin: 0 0 20px 0;
  }
}

.gym__info span {
  font-weight: 400;
  font-size: 18px;
  line-height: 18px;
  margin: 0 0 44px 0;
  display: block;

  @include vp-1199 {
    max-width: 480px;
    margin: 0 auto 39px auto;
  }

  @include vp-767 {
    font-size: 18px;
    line-height: 20px;
    max-width: none;
    margin: 0 0 40px 0;
  }
}

.gym__info p {
  font-size: 16px;
  line-height: 24px;
  margin: 0 0 8px 0;
  display: block;

  @include vp-1199 {
    max-width: 510px;
    margin: 0 auto;
  }

  @include vp-767 {
    max-width: none;
    margin: 0;
  }
}

.gym__info p:not(:first-of-type) {
  @include vp-1199 {
    display: none;
  }
}

.gym__media-wrapper {
  position: relative;

  @include vp-1199 {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
  }

  @include vp-767 {
    flex-direction: column;
    align-items: center;
    margin: 0 -15px 0 -15px;
  }
}

.gym__image-wrapper {
  margin-bottom: 83px;

  @include vp-767 {
    margin: 0 -15px 40px -15px;
  }
}

.gym__image-wrapper img {

  @include vp-1199 {
    width: 678px;
    height: 345px;
  }

  @include vp-767 {
    width: 100vw;
    height: 100%;
  }
}

.gym__video-wrapper {
  width: 364px;
  height: 226px;
  position: absolute;
  top: 289px;
  left: -63px;

  @include vp-1199 {
    width: 266px;
    height: 167px;
    top: auto;
    bottom: 0;
    left: 50%;
    transform: translate(-50%);
  }

  @include vp-767 {
    width: 100vw;
    height: 100%;
    position: relative;
    transform: none;
    top: 0;
    left: 0;
  }
}

.gym__video-link {
  position: relative;

  &::before {
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;

    background-color: rgba($color-dark-cerulean, 0.5);
  }
}

.gym__video-wrapper--is-enabled .gym__video-button {
  display: block;
}

.gym__video-wrapper img {
  @include vp-1199 {
    width: 266px;
    height: 167px;
  }

  @include vp-767 {
    width: 100%;
    height: 100%;
  }
}

.gym__video {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  border: none;

  @include vp-767 {
    position: static;
  }
}

.gym__video-button {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 100px;
  height: 100px;
  padding: 0;
  border: none;
  border-radius: 50%;
  background-image: url("../img/svg/button-border.svg");
  background-size: 100%;
  background-repeat: no-repeat;
  background-color: $color-transparent;
  z-index: 1;
  display: none;
  cursor: pointer;

  &::before {
    content: "";
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    border-radius: 50%;
    width: 72px;
    height: 72px;

    background-image: $red-blue-linear-diagonal;
    background-repeat: no-repeat;
    background-size: 100%;
  }

  &::after {
    content: "";
    position: absolute;
    top: 42px;
    left: 46px;
    width: 13px;
    height: 15px;
    clip-path: polygon(0 0, 0% 100%, 100% 50%);

    background-color: $color-default-white;
  }

  &:hover,
  &:focus {
    background-image: none;
    border: 2px solid $color-sapphire;

    &::before {
      background-image: none;
      background-color: $color-default-white;
    }

    &::after {
      background-color: $color-sapphire;
      top: 40px;
      left: 44px;
    }
  }

  &:focus-visible {
    outline: none;
  }

  &:active {
    background-image: none;
    border: 2px solid $color-sapphire;

    &::before {
      background-image: none;
      background-color: $color-default-white;
      width: 58px;
      height: 58px;
    }

    &::after {
      background-color: $color-sapphire;
      top: 40px;
      left: 44px;
    }
  }

  &:disabled {
    background-image: none;
    border: 2px solid rgba($color-sapphire, 0.4);
    pointer-events: none;

    &::before {
      background-image: none;
      background-color: $color-default-white;
    }

    &::after {
      background-color: rgba($color-sapphire, 0.4);
      top: 40px;
      left: 44px;
    }
  }

  @include vp-1199 {
    width: 73px;
    height: 73px;

    &::before {
      width: 52px;
      height: 52px;
    }

    &::after {
      top: 31px;
      left: 33px;
      width: 9px;
      height: 10px;
    }

    &:hover,
    &:focus {
      &::after {
        top: 29px;
        left: 31px;
      }
    }

    &:active {

      &::before {
        width: 31px;
        height: 31px;
      }

      &::after {
        top: 29px;
        left: 31px;
      }
    }

    &:disabled {

      &::after {
        top: 29px;
        left: 31px;
      }
    }
  }
}
